.list {
  max-height: 300px;
}
.list:not(.is-popup) {
  max-height: 400px;
}

.default-list.is-selected-all {
  background: var(--bg-color-grey-3);
  cursor: not-allowed;
}
.default-list.is-selected-all .item-label {
  color: var(--font-color-grey-2);
}
.default-list.is-selected-all .list-item {
  pointer-events: none;
}

.default-list-item {
  padding: 5px 12px !important;
  border-bottom: none;
  cursor: pointer;
  transition: background 300ms;
  position: relative;
  border-bottom-color: transparent !important;
}
.default-list-item:not(.is-selected):hover {
  background: var(--bg-color-grey-1);
}
.default-list-item.is-selected {
  background: var(--primary-color-1);
}
.default-list-item.is-selected :global(.select-list-item-label),
.default-list-item.is-selected .single-checked {
  color: var(--primary-color);
}
.default-list-item.is-disabled {
  color: var(--font-color-grey-2);
  cursor: not-allowed;
}
.default-list-item :global(.select-list-item-label),
.default-list-item :global(.select-list-item-description) {
  transition: color 300ms;
}
.default-list-item :global(.select-list-item-description) {
  color: var(--font-color-grey-1);
}

.single-checked {
  flex-basis: 14px;
}

.selected-all:global(.is-popup) {
  padding: 0 12px;
}
.selected-all:global(.is-popup) .selected-all {
  margin: 5px 0;
}
.selected-all:not(:global(.is-popup)) {
  padding: 5px 0;
}

.selected-tag {
  border-top: solid 1px var(--font-color-grey-4);
}
.selected-tag:global(.is-popup) {
  padding: 12px 0 12px 12px;
}
.selected-tag:not(:global(.is-popup)) {
  padding: 12px 0 12px;
}

.select-search {
  display: flex;
}
.select-search:global(.is-popup) {
  padding: 12px;
}